import { AccordionTypes } from "../Types";
import { AccordionContext } from "../Context/Context";
import { _useAccordion } from "./useAccordion";

export const Provider: AccordionTypes['Parts']['Provider'] = ({ children }) => {
  const accordionState = _useAccordion()

  return (
    <AccordionContext.Provider value={accordionState}>
      {children}
    </AccordionContext.Provider>
  )
}